"use client";
import contentpt from "@/assets/contentpt.json";
import contenten from "@/assets/contenten.json";
import { useLanguage } from "../header/LanguageContext";
import Image from "next/image";

function Products() {
  const { language } = useLanguage();
  const content = language === "pt" ? contentpt : contenten;
  const showcase = content.productsShowcase;

  const gridClasses = [
    "md:col-span-2 md:row-span-1",
    "md:col-start-1 md:row-start-2 md:row-span-2",
    "md:col-start-2 md:row-start-2",
    "md:col-start-2 md:row-start-3",
  ];

  return (
    <div className="mt-10">
        <div className="p-5 max-w-200 lg:max-w-250 m-auto flex flex-col md:grid md:grid-cols-2 md:grid-rows-3 gap-10">
          {showcase.products.map((product, index) => (
            <div
              key={index}
              className={`bg-(--gray) rounded-[35px] hover:shadow-lg cursor-pointer flex flex-col justify-center px-10 ${gridClasses[index]} ${index === 0 && "h-80 items-end justify-center flex-row"} ${index === 1 ? "items-center justify-center" : "items-center justify-start"} ${index === 2 && "flex-col-reverse"} ${index === 3 && "flex-col-reverse"}`}
            >
              <Image
                src={product.image}
                alt={`Produto ${index + 1}`}
                width={2000}
                height={2000}
                className={`${index === 0 && "w-80"}`}
              />
              <p className="self-center text-center text-[1.2em] font-medium">{product.text}</p>
            </div>
          ))}
        </div>
    </div>
  );
}

export default Products;